import axios from "axios";

type EndObj = {ENDOBJ_NM: string; ENDOBJ_SCAN: string;};
type Switch = {[key: string]: EndObj[];}; //key=SW_LBL
export async function getSparql(panel_fmid: string): Promise<[string, Switch]> {
    try {
        //TODO: URL
        const baseUrl = 'https://poc.sdm.kke.co.jp';
        //panel_fmid = 'hoge';

        const query = `
            PREFIX brick: <https://brickschema.org/schema/Brick#>
            PREFIX rdf: <http://www.w3.org/1999/02/22-rdf-syntax-ns#>
            PREFIX rdfs: <http://www.w3.org/2000/01/rdf-schema#>
    
            select distinct ?PANEL_NM, ?SW_LBL, ?ENDOBJ_NM, ?ENDOBJ_SCAN where{
            #電灯盤取得
            ?PANEL <http://www.semanticweb.org/chensisi/ontologies/2022/9/untitled-ontology-163:OWLObjectProperty_c91c8dae_12f9_45ab_ac9e_35a81e8c9dc3> "${panel_fmid}"@ja. #FMID：検索キー
            ?PANEL rdfs:label ?PANEL_NM.
    
            #電灯盤SW取得
            ?PANEL brick:hasPart ?SW.
            ?SW rdfs:label ?SW_NM.
            ?SW <http://www.semanticweb.org/chensisi/ontologies/2022/9/untitled-ontology-163:OWLObjectProperty_d7843f2a_8710_4d42_bbe7_20f29037fd8e> ?SW_MEMO. #※備考
            ?SW <http://www.semanticweb.org/chensisi/ontologies/2022/9/untitled-ontology-163:OWLObjectProperty_7dce3876_b400_4a5a_a6d4_e37c5f2f2a1d> ?SW_LBL. #★ラベル：水色ボタンに表示
    
            #回路取得
            ?SW brick:feeds ?CIRC.
            ?CIRC rdfs:label ?CIRC_NM.
    
            #末端オブジェクト取得
            ?CIRC brick:feeds ?ENDOBJ.
            ?ENDOBJ rdfs:label ?ENDOBJ_NM. #末端オブジェクト名称：白ボタンに表示
            ?ENDOBJ <http://www.semanticweb.org/chensisi/ontologies/2022/9/untitled-ontology-163:OWLObjectProperty_cbdc3161_268f_4b71_8f71_5727436a50ba> ?ENDOBJ_SCAN. #3DスキャンURL：白ボタンにリンク埋め込み
            }
        `;
        const url = baseUrl + '?query=' + encodeURIComponent(query);
  
        // APIを呼び出す
        const response = await axios.get(url);

        // 取得結果を設定（電灯盤SWをkeyとする、末端オブジェクトの配列を生成）
        const resData:Switch = {};
        let panel_name = '';
        response.data.results.bindings.forEach((value:any) => {
            if(!panel_name){
                panel_name = value['PANEL_NM']['value'];
            }

            if(!(value['SW_LBL']['value'] in resData)){
                resData[value['SW_LBL']['value']] = [];
            }
            resData[value['SW_LBL']['value']].push({ENDOBJ_NM: value['ENDOBJ_NM']['value'], ENDOBJ_SCAN: value['ENDOBJ_SCAN']['value']});
        });

        return [panel_name, resData];
    } catch (error) {
        //TODO: エラー処理
        console.error(error);
        return ['Error', {}]
    }
}
