
import { defineComponent, reactive } from 'vue';
import { getSparql } from '@/services/SparqlApiService';

//参考HP
//https://arrown-blog.com/vuejs-dropdownmenu/
//https://tedate.jp/vue-js/how-to-changing-the-data-part-in-vue3

export default defineComponent({
  name: 'Menu',
  props: {
    msg: String,
    panel_fmid: String,
  },
  setup(props){
    const data = reactive({
      panel_name:"",
      treedata:[{}],//ボタンを表示する階層データ
      active:""
    })

    //画面に表示する階層構造のデータをセット（ToDo:SPQRQLエンドポイントより取得できなかった場合の処理を記載）
    getSparql((props.panel_fmid) as string).then(([panel_name, sparqlData]) => {
      data.panel_name = panel_name;

      type bottom_obj = {obj_name: string; obj_url: string;};
      const treedata:{sw_name: string; bottom_objs: bottom_obj[]}[] = [];

      // key(電灯盤SW)単位で繰り返し、末端オブジェクトの配列を設定
      Object.keys(sparqlData).forEach((key: string) => {
        const bottom_objs:bottom_obj[] = [];
        sparqlData[key].forEach((value: any) => {
          bottom_objs.push(
            {
              obj_name: value['ENDOBJ_NM'],
              obj_url: value['ENDOBJ_SCAN']
            }
          );
        });
        
        treedata.push(
          {
            sw_name: key,
            bottom_objs: bottom_objs
          }
        );
      });

      data.treedata = treedata;
    });

    //method
    //https://tech.synapse.jp/entry/2022/08/15/113000
    function dropdown(list:string) {
        console.log(data.active);
        data.active = data.active===list ? "":list;
    }
    
    return{
      data,
      dropdown
    }
  }
});
