
import { defineComponent, reactive } from 'vue';
import Menu from '@/components/Menu.vue'; // @ is an alias to /src
import { useRoute } from 'vue-router'; 

export default defineComponent({
  name: 'switch',
  components: {
    Menu,
  },
  setup(){
    //data
    const data = reactive({
      panel_fmid:""
    })

    //URLのパラメータの値を取得
    //参考
    //https://codelikes.com/use-vue-router/#toc4
    //接続時のサンプルURL
    //http://localhost:8080/switch/E_LB_105
    const route = useRoute();
    data.panel_fmid = (route.params.fmid) as string;

    return{
      data
    }
  }
});
